$black: #211f20;
$blue: #01204e;
$yellow: #f19308;

.Testimonials {
  margin: 2em;
  h2 {
    text-align: center;
    color: $blue;
  }
  .container {
    //max-width: 100%; /* Set the width of the container */
    //max-height: 50vh;
    overflow-x: auto; /* Enable horizontal scrolling */
    display: flex;
    align-items: center;
    scroll-snap-type: x mandatory; /* Enable mandatory snapping on the x-axis */
    padding: 1em;
    overflow-y: hidden;
  }
  .text {
    margin: 0 1em;
    padding: 2em;
    min-width: 80%;
    height: 100%;
    word-wrap: break-word;
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 30px 5px #211f2030;
    border-radius: 10px;
    scroll-snap-align: end;
    svg {
      fill: $blue;
      width: 10%;
    }
    h3 {
      width: 100%;
      //align-self: center;
      margin: 1em 0;
      font-weight: lighter;
    }
    p {
      color: $yellow;
    }
    svg:nth-of-type(2) {
      transform: rotate(180deg);
      align-self: flex-end;
    }
  }
}
@media screen and (max-width: 400px) {
}
