$black: #211f20;
$blue: #01204e;
$yellow: #f19308;

.Header {
  position: sticky;
  z-index: 2;
  top: 0.5em;
  left: 0;
  display: flex;
  margin: 0.5em;
  padding: 1em;
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 30px 5px #211f2030;
  min-height: 100%;
  background-color: #fff;
  .menu {
    ul {
      list-style: none;
      display: flex;
      a {
        text-decoration: none;
      }
      li {
        margin: 0 2em;
        cursor: pointer;
        h2 {
          font-size: 1.5em;
          color: $black;
        }
      }
    }
  }
  .phone {
    background-color: $yellow;
    padding: 1em;
    text-align: center;
    border-radius: 15px;
    cursor: pointer;
    p {
      color: #fff;
    }
    a {
      color: #fff;
      text-decoration: none;
    }
  }
}
.frontLogo {
  width: 6%;
  cursor: pointer;
  justify-self: center;
}
.burgerIcon {
  display: none;
  width: 15%;
  cursor: pointer;
}
.sideMenu {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 90%; /* Adjust as needed */
  background-color: #fff;
  box-shadow: 0 4px 30px 5px #211f2030;
  padding: 1em;
  .imgs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1em 0;
    .logo {
      width: 15%;
      cursor: pointer;
    }
    .close {
      width: 5%;
      cursor: pointer;
    }
  }
  hr {
    border: none;
    border-bottom: solid 2px $blue;
  }
  .menu2 {
    margin: 5em 2em;
    ul {
      list-style: none;
      li {
        color: $black;
        margin: 2em 0;
        cursor: pointer;
        a {
          text-decoration: none;
        }
      }
    }
  }
}
@media screen and (max-width: 1300px) {
  .Header {
    .menu {
      ul {
        li {
          h2 {
            font-size: .9em;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1000px) {
  .Header {
    .menu {
      ul {
        li {
          h2 {
            font-size: .7em;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 900px) {
  .Header {
    .menu {
      display: none;
    }
    .phone {
      padding: 0.5em 1em;
    }
  }
  .frontLogo {
    width: 15%;
    cursor: pointer;
  }
  .burgerIcon {
    display: inline-block;
  }
}
@media screen and (max-width: 500px) {
  .Header {
    .frontLogo {
      width: 20%;
      cursor: pointer;
      margin: 0 0 0 1.5em;
    }
    .phone {
      font-size: 0.7em;
    }
    .burgerIcon {
      width: 15%;
    }
  }
}
@media screen and (max-width: 350px) {
  .Header {
    .phone {
      font-size: 0.5em;
    }
  }
  .sideMenu {
    .menu2 {
      font-size: 0.6em;
    }
  }
}
