$black: #211f20;
$blue: #01204e;
$yellow: #f19308;

.Projects {
  padding: 1em;
  background-color: $blue;
  position: relative;
  h2 {
    text-align: center;
    margin: 1em 0;
    color: #fff;
  }
  img {
    border-radius: 5px;
    object-fit: cover;
    box-shadow: 0 4px 30px 5px #211f2030;
    cursor: pointer;
  }
  .viewImg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%; /* Make the overlay fill 90% of the screen width */
    //max-width: 1000px; /* Limit the maximum width of the overlay */
    z-index: 1; /* Ensure the image is on top of other content */
    text-align: center;
    img {
      cursor: default;
      box-shadow: 0 4px 30px 5px #211f2090;
      border-radius: 0px;
      width: 100%; /* Make the image fill the entire width of the overlay */
      height: auto;
    }
    button {
      width: 30%;
      margin: 2em;
      padding: 1em 0;
      border-radius: 20px;
      border: none;
      border: solid 2px $yellow;
      color: $yellow;
      background: none;
      cursor: pointer;
    }
  }
}
