$black: #211f20;
$blue: #01204e;
$yellow: #f19308;

.Services {
  b {
    color: $yellow;
  }
  padding: 2em;
  ul {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    list-style: none;
    li {
      margin: 1em;
      cursor: pointer;
      .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #fff;
        background-color: $yellow;
        padding: 0.5em;
        border-radius: 15px;
        .text {
          display: flex;
          align-items: center;
          h3 {
            margin: 0 0.5em;
          }
        }
        svg {
          fill: #fff;
          width: 10%;
        }
      }
      p {
        display: none;
        margin: 1em;
        box-shadow: 0 4px 30px 5px #211f2030;
        padding: 1em;
        border-radius: 15px;
      }
    }
  }
}
@media screen and (max-width: 1300px) {
  .Services {
    ul {
      grid-template-columns: repeat(3, 1fr);
      li {
        .container {
          svg {
            width: 20%;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 900px) {
  .Services {
    ul {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}
@media screen and (max-width: 600px) {
  .Services {
    ul {
      grid-template-columns: repeat(1, 1fr);
      li {
        margin: 0.5em 0;
      }
    }
  }
}
@media screen and (max-width: 400px) {
  .Services {
    padding: 1em;
  }
}
