$black: #211f20;
$blue: #01204e;
$yellow: #f19308;
.Home {
  height: 100vh;
  display: flex;
  padding: 2em;
  align-items: center;
  .text {
    width: 50%;
    margin: 2em;
    h1 {
      color: $blue;
    }
    h1:nth-of-type(2) {
      //text-align: center;
      margin: 0 0 1em 0;
    }
    b {
      color: $yellow;
    }
    .btn {
      width: 100%;
      display: flex;
      justify-content: center;
      button {
        width: 50%;
        background-color: $yellow;
        border: none;
        border-radius: 50px;
        padding: 1em 2em;
        cursor: pointer;
        p {
          color: #fff;
        }
        a {
          text-decoration: none;
          color: #fff;
        }
      }
    }
  }
  .imgs {
    width: 50%;
    img {
      width: 100%;
    }
  }
}
@media screen and (max-width: 1300px) {
  .Home {
    .text {
      h1 {
        font-size: 3em;
      }
    }
  }
}
@media screen and (max-width: 1000px) {
  .Home {
    flex-direction: column;
    height: 100%;
    .text {
      width: 80%;
      margin: 2em 0;
      h1 {
        font-size: 4em;
      }
    }
    .imgs {
      width: 90%;
    }
  }
}
@media screen and (max-width: 700px) {
  .Home {
    .text {
      h1 {
        font-size: 2.5em;
      }
    }
  }
}
@media screen and (max-width: 500px) {
  .Home {
    .text {
      width: 100%;
      margin: 2em 0;
      h1 {
        font-size: 2em;
      }
    }
    .imgs {
      width: 100%;
    }
  }
}
@media screen and (max-width: 350px) {
  .Home {
    .text {
      .btn {
        button {
          width: 70%;
          padding: 0.5em 2em;
        }
      }
    }
  }
}
