$black: #211f20;
$blue: #01204e;
$yellow: #f19308;

.AboutPage {
  background-color: $yellow;
  color: #fff;
}
.About {
  padding: 2em;
  b {
    color: $blue;
  }
  .theDivide {
    display: flex;

    .first {
      width: 50%;
      margin: 1em;
      p {
        margin: 1em;
      }
    }
    .second {
      width: 50%;
      margin: 1em;
      ul {
        list-style: none;
        li {
          display: flex;
          align-items: center;
          margin: 1em;
          p {
            margin: 0 1em;
            width: 100%;
          }
          svg {
            width: 5%;
          }
        }
      }
      .imgs {
        width: 100%;
        height: 50%;
        margin: 1em;
        text-align: center;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 5px;
        }
      }
    }
  }
}
.nhbrc {
  background-color: $blue;
  padding: 1em;
  text-align: center;
  p {
    margin: 1em 0;
  }
  svg {
    width: 10%;
    fill: #fff;
  }
}
@media screen and (max-width: 900px) {
  .About {
    .theDivide {
      flex-direction: column;
      .first {
        width: 100%;
      }
      .second {
        width: 100%;
        .imgs {
          margin: 0 0;
          img {
            width: 90%;
          }
        }
      }
    }
  }
  .nhbrc {
    svg {
      width: 20%;
    }
  }
}
@media screen and (max-width: 700px) {
  .AboutPage {
    p {
      font-size: 1em;
    }
  }
}
@media screen and (max-width: 400px) {
  .About {
    padding: 1em;
    .first,
    .second {
      margin: 0.5em;
    }
  }
  .nhbrc {
    padding: 0.5em;
    svg {
      width: 30%;
    }
  }
}
