$black: #211f20;
$blue: #01204e;
$yellow: #f19308;

.Contact {
  background-color: $yellow;
  padding: 1em;
  h2 {
    text-align: center;
    color: #fff;
    margin: 1em 00;
  }
  .container {
    display: flex;
    justify-content: space-between;
    .first {
      padding: 1em;
      width: 50%;
      iframe {
        border-radius: 10px;
        border: none;
        width: 100%;
        height: 50vh;
        outline: none;
      }
      button {
        width: 100%;
        padding: 1em 0;
        a {
          display: flex;
          justify-content: center;
          align-items: center;
          color: #fff;
          text-decoration: none;
        }
        border-radius: 10px;
        margin: 1em 0;
        border: none;
        cursor: pointer;
        svg {
          width: 5%;
          margin: 0 0.5em;
          fill: #fff;
        }
      }
      button:nth-of-type(1) {
        background-color: #04d031;
      }
      button:nth-of-type(2) {
        background-color: $blue;
      }
    }
    form {
      padding: 1em;
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      h3 {
        text-align: center;
        font-weight: lighter;
        margin: 1em 0;
        color: #fff;
      }
      p{
        b{
          color: #ff1000;
        }
      }
      input,
      textarea {
        width: 80%;
        margin: 1em;
        padding: 1em;
        background: none;
        border-radius: 5px;
        background-color: #fff;
        border: none;
        outline: none;
      }
      button {
        width: 60%;
        margin: 1em;
        padding: 1em;
        background: none;
        border-radius: 50px;
        background-color: $blue;
        border: none;
        color: #fff;
        cursor: pointer;
      }
    }
  }
}
@media screen and (max-width: 1200px) {
  .Contact {
    .container {
      flex-direction: column;
      .first,
      form {
        width: 100%;
      }
    }
  }
}
@media screen and (max-width: 500px) {
  .Contact {
    .container {
      .first {
        iframe {
          height: 20vh;
        }
      }
    }
  }
}
